@mixin mq($type) {
    $mobile: 320px;
    $tablet: 768px;
    $desktop: 1280px;
    
    $event-section: 500px;

    // @if $type =='mobile-fixed' {
    //     @media screen and (min-width: 480px) and (max-width: 767.98px) {
    //         @content;
    //     }
    // }
    @if $type =='mobile-only' {
        @media screen and (max-width: #{$tablet - 0.02}) {
            @content;
        }
    }

    @if $type =='mobile' {
        @media screen and (min-width: $mobile) {
            @content;
        }
    }


    @if $type =='tablet-only' {
        @media screen and (min-width: $tablet) and (max-width: #{$desktop - 0.02}) {
            @content;
        }
    }

    @if $type =='tablet' {
        @media screen and (min-width: $tablet) {
            @content;
        }
    }

    @if $type =='not-desktop' {
        @media screen and (max-width: #{$desktop - 0.02}) {
            @content;
        }
    }

    @if $type =='desktop' {
        @media screen and (min-width: $desktop) {
            @content;
        }
    }

    @if $type =='event-section' {
        @media screen and (min-width: $event-section) and (max-width: #{$tablet - 0.02}) {
            @content;
        }
    }

   @if $type =='retina' {
       @media (min-device-pixel-ratio: 2),
       (min-resolution: 192dpi),
       (min-resolution: 2dppx) {
           @content;
       }
   }
}

@mixin fonts($fw, $fs, $lh, $tt: false) {
    font-weight: $fw;
    font-size: $fs;
    line-height: $lh;

    @if $tt {
        text-transform: $tt;
    }
}